body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  font-size: 10px; }

.App {
  text-align: center;
  background: url(/location-weather-app/static/media/sunny.e0640165.jpg);
  background-size: cover; }

* {
  box-sizing: border-box; }

h1 {
  font-size: 5.8rem;
  line-height: 7rem;
  padding: 0 1rem; }

p {
  font-size: 4rem;
  max-width: 50.5rem;
  padding: 0 2.5rem;
  margin-top: 0; }

.App-wrapper.sunny {
  background: url(/location-weather-app/static/media/sunny.e0640165.jpg);
  background-size: cover; }

.App-wrapper.wind, .App-wrapper.drizzle, .App-wrapper.clouds {
  background: url(/location-weather-app/static/media/rainy.584a02f7.jpg);
  background-size: cover; }

.App-body {
  font-family: "Quicksand", sans-serif;
  background-size: cover;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: #f7f7f7;
  text-shadow: 0 3px 4.45px rgba(0, 0, 0, 0.17); }

footer {
  color: white;
  padding-bottom: 2rem;
  font-family: "Quicksand", sans-serif;
  font-size: 2rem; }
  footer a {
    color: white; }

